type User = {
  id: number;
  email: string;
  password: string;
  role: 'user' | 'admin';
};

type Service = {
  id: number;
  name: string;
};

type Booking = {
  id: number;
  user: string;
  serviceId: number;
  dateTime: string;
};

// Dummy-DB
export const services: Service[] = [
  { id: 1, name: 'Haarschnitt' },
  { id: 2, name: 'Bart trimmen' },
  { id: 3, name: 'Massage' },
];

// 🔁 Lade gespeicherte Daten aus localStorage (einmalig beim Import)
let users: User[] = []
let bookings: Booking[] = []

if (typeof window !== 'undefined') {
  users = JSON.parse(localStorage.getItem('users') || '[]')
  bookings = JSON.parse(localStorage.getItem('bookings') || '[]')
}


let userIdCounter = users.length + 1
let bookingIdCounter = bookings.length + 1

// 🔐 Helper: speichern
function saveUsers() {
  if (typeof window !== 'undefined') {
    localStorage.setItem('users', JSON.stringify(users))
  }
}

function saveBookings() {
  if (typeof window !== 'undefined') {
    localStorage.setItem('bookings', JSON.stringify(bookings))
  }
}


export function registerUser(email: string, password: string): { success: boolean; message: string } {
  const exists = users.find(user => user.email === email)
  if (exists) {
    return { success: false, message: 'User already exists' }
  }
  const role = email === 'admin@booking.com' ? 'admin' : 'user'
  users.push({ id: userIdCounter++, email, password, role })
  saveUsers()
  return { success: true, message: 'User registered successfully' }
}

export function loginUser(email: string, password: string): { success: boolean; message: string } {
  const user = users.find(u => u.email === email && u.password === password)
  if (!user) {
    return { success: false, message: 'Invalid credentials' }
  }
  return { success: true, message: 'Login successful' }
}

export function getUserByEmail(email: string): User | undefined {
  return users.find(user => user.email === email)
}

export function createBooking(user: string, serviceId: number, dateTime: string): Booking {
  const booking = {
    id: bookingIdCounter++,
    user,
    serviceId,
    dateTime,
  }
  bookings.push(booking)
  saveBookings()
  return booking
}

export function getUserBookings(user: string): Booking[] {
  return bookings.filter((b) => b.user === user)
}

export function getAllBookings(): Booking[] {
  return bookings
}
